* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
}

:root {
  --mainColor: #c9c9cd;
  --mainColorLight: #9aa2c3;
  --secondaryColor: rgb(243, 158, 158);
  --textColor: black;
}

header {
  display: flex;
  height: 60px;
  width: 100%;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
  align-items: center;
  justify-content: space-between;
  gap: "20%";
}
nav {
  display: flex;
  height: auto;
  width: 100%;
  align-items: center;
  justify-content: space-evenly;
}
nav Link {
  color: var(--textColor);
  text-decoration: none;
  height: auto;
  justify-content: space-between;
}
nav Link:hover {
  color: var(--secondaryColor);
}
header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
}

@media only screen and (max-width: 1024px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
    font-size: 1.8rem;
  }
  header nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 40%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: var(--mainColor);
    transition: 1s;
    transform: translateY(-100vh);
  }
  header .responssive_nav {
    transform: none;
  }
  nav .nav-close-btn {
    position: absolute;
    padding-bottom: 50rem;
    top:5px;
    right:8px;
  }
  nav Link {
    font-size: 1.5rem;
  }
}
